import { BaseModel } from "./base.model";
import { GenderEnum } from "../enum/gender.enum";
import { PronounEnum } from "../enum/pronun.enum";

export class UserModel extends BaseModel {
  name: string;
  lastName: string;
  email: string;
  dateOfBirth: string;
  genderEnum?: GenderEnum;
  pronoun?: PronounEnum;
  customGender?: string;

  constructor() {
    super();
    this.name = "";
    this.lastName = "";
    this.email = "";
    this.dateOfBirth = "";
    this.genderEnum = null;
    this.pronoun = null;
    this.customGender = null;
  }
}
